import React from "react";
import axios from "axios";
import { Button, Form, Label } from "react-bootstrap";

class ContributionForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { autor: "", titel: "", text: "" };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    const target = event.target;
    const name = target.name;
    this.setState({
      [name]: target.value,
    });
  }
  handleSubmit(event) {
    event.preventDefault();
    const newContribution = {
      autor: this.state.autor,
      titel: this.state.titel,
      text: this.state.text,
      zeit: new Date().toString(),
    };
    axios
      .post("http://localhost:5000/Contribution/add", newContribution)
      .then((res) => console.log(res.data))
      .catch((err) => console.log(err));

    this.setState({
      autor: "",
      titel: "",
      text: "",
    });
  }

  render() {
    return (
      <Form className="d-flex flex-column mx-5" onSubmit={this.handleSubmit}>
        {" "}
        <Form.Label>Autor:</Form.Label>
        <Form.Control
          name="autor"
          type="text"
          value={this.state.autor}
          onChange={this.handleChange}
        />{" "}
        <Form.Label>Titel:</Form.Label>
        <Form.Control
          name="titel"
          type="text"
          value={this.state.titel}
          onChange={this.handleChange}
        />{" "}
        <Form.Label>Text</Form.Label>
        <Form.Control
          as="textarea"
          name="text"
          value={this.state.text}
          onChange={this.handleChange}
        />
        <Button className="mt-3" type="submit" value="Absenden">
          {" "}
          Absenden{" "}
        </Button>
      </Form>
    );
  }
}

export default ContributionForm;
